article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

img {
  width: 100%;
  height: 100%;
}

.currentStatusList {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 4rem;
  text-align: center;
}

.currentStatusList li {
  line-height: 4.5rem;
  font-size: 2.5rem;
}

header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav {
  width: 100%;
  height: 10rem;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
}

.nav ul {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav a {
  font-size: 3.8rem;
  text-decoration: none;
  color: black;
}

h1 {
  font-size: 7rem;
  margin: 0 0 3rem;
  width: 90%;
  text-align: center;
}

.date {
  font-size: 5rem;
  text-align: center;
  margin: 3rem 0;
}

.statistic,
.news {
  margin: 10rem auto 5rem;
  width: 80%;
}

.searchFormContainer {
  margin-top: 3rem;
}

.searchFormContainer form {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5.5rem;
}

.searchFormContainer input {
  width: 50%;
  font-size: 3rem;
  height: 100%;
}

.searchFormContainer button {
  width: 20rem;
  font-size: 3rem;
  height: 100%;
}

table {
  margin-top: 3rem;
  width: 100%;
}

.searchedCountry {
  color: blue;
}

tr {
  border-bottom: 1px solid black;
  height: 6rem;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

th {
  font-size: 2.5rem;
  width: calc(100% / 6);
}

td {
  font-size: 1.8rem;
  width: calc(100% / 6);
}

.totalCases,
.totalDeaths {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.totalCases span:nth-child(2),
.totalDeaths span:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
}

.arrow {
  color: red;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 6px solid red;
  margin-right: 0.15rem;
} 

.newsList {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 auto;
  list-style: none;
}

.newsList li {
  width: 31%;
  border: 2px solid black;
  padding: 2rem;
  margin-bottom: 5rem;
  border-radius: 1rem;
}

.newsList li:nth-child(3n + 2) {
  margin: 0 3.5% 5rem;
}

h3 {
  font-size: 2rem;
  margin-top: 0;
}

.newsArticle {
  margin: 2rem 0;
}

.newsList p {
  font-size: 1.3rem;
  margin: 0;
}

.newsList p:nth-child(5) {
  font-size: 1.6rem;
  margin-bottom: 2rem;
}

.newsList a {
  font-size: 1.6rem;
}

.question1,
.question2,
.question3,
.question4,
.question5 {
  display: none;
}


@media(max-width: 1120px) {
  h1 {
    font-size: 6.5rem;
  }

  .date {
    font-size: 4.5rem;
  }


  .nav a {
    font-size: 3.5rem;
  }

  .news {
    width: 90%;
  }

  .newsList {
    justify-content: space-between;
  }

  .newsList li:nth-child(3n + 2) {
    margin: 0 0 5rem;
  }

  .newsList li {
    width: 48%;
  }

  .statistic {
    width: 95%;
  }

  th {
    font-size: 2.2rem;
  }

  td {
    font-size: 1.6rem;
  }
}

@media(max-width: 768px) {
  .news {
    width: 95%;
  }

  .newsList li {
    width: 100%;
  }
  
  .statistic {
    margin-top: 10rem;
  }

  .date {
    margin: 2rem 0;
  }

  .searchFormContainer {
    margin-top: 3rem;
  }

  .searchFormContainer input {
    font-size: 2.5rem;
  }

  .searchFormContainer button {
    width: 15rem;
    font-size: 2.5rem;
  }

  th {
    font-size: 2rem;
  }
}

@media(max-width: 640px) {
  h1 {
    font-size: 5.5rem;
    margin-bottom: 2rem;
  }

  .date {
    font-size: 4rem;
  }

  .currentStatusList li {
    font-size: 2.2rem;
    line-height: 4rem;
  }

  .nav a {
    font-size: 3rem;
  }
  .searchFormContainer form {
    height: 5rem;
  }

  .searchFormContainer input {
    width: 70%;
    font-size: 2.2rem;
  }
  
  .searchFormContainer button {
    width: 30%;
    font-size: 2.2rem;
  }

  th:nth-child(4),
  th:nth-child(5),
  td:nth-child(4),
  td:nth-child(5) {
    display: none;
  }

  th,
  td {
    width: calc(100% / 4);
  }
}

@media(max-width: 430px) {
  h1 {
    font-size: 5rem;
    width: 100%;
  }

  .currentStatusList li {
    font-size: 2rem;
    line-height: 3.6rem;
  }

  .nav a {
    font-size: 2.7rem;
  }

  .newsList li {
    margin-bottom: 3rem;
  }

  .searchFormContainer input {
    width: 65%;
  }
  
  .searchFormContainer button {
    width: 35%;
  }

  tr {
    height: 5.5rem;
  }

  th {
    font-size: 1.7rem;
  }

  td {
    font-size: 1.45rem;
  }
}